import React from 'react';
import { Field, CLS, SectionHeader } from '../ui/BlockBase';
import { PrestacaoContas } from '../../schemas/type-definitions';

export const DescritorBlock = ({ data, updateField }: { data: PrestacaoContas, updateField: Function }) => (
    <div className={CLS.SECTION}>
        <SectionHeader title="1. Descritor da Prestao" />
        <Field 
            label="Tipo de Documento" 
            value={data.descritor.tipo_documento} 
            onChange={(v: any) => updateField('descritor.tipo_documento', v)}
            options={[
                { value: "Prestao de Contas de Convnio", label: "Prestao de Contas de Convnio" },
                { value: "Prestao de Contas de Contrato de Gesto", label: "Prestao de Contas de Contrato de Gesto" },
                { value: "Prestao de Contas de Termo de Parceria", label: "Prestao de Contas de Termo de Parceria" },
                { value: "Prestao de Contas de Termo de Fomento", label: "Prestao de Contas de Termo de Fomento" },
                { value: "Prestao de Contas de Termo de Colaborao", label: "Prestao de Contas de Termo de Colaborao" },
                { value: "Declarao Negativa", label: "Declarao Negativa" }
            ]}
        />
        <div className={CLS.GRID_CONTAINER + " mt-4"}>
            <Field label="Municpio (Cdigo IBGE)" type="number" value={data.descritor.municipio} onChange={(v: any) => updateField('descritor.municipio', v)} />
            <Field label="Entidade (Cdigo TCESP)" type="number" value={data.descritor.entidade} onChange={(v: any) => updateField('descritor.entidade', v)} />
            <Field label="Ano de Referncia" type="number" value={data.descritor.ano} onChange={(v: any) => updateField('descritor.ano', v)} />
            <Field label="Ms de Referncia" type="number" value={data.descritor.mes} onChange={(v: any) => updateField('descritor.mes', v)} />
        </div>
        <p className="mt-4 text-xs text-slate-500 italic bg-blue-50 p-2 rounded">
            Certifique-se de que o Cdigo da Entidade e o Municpio correspondam exatamente ao seu cadastro no TCESP para evitar rejeio do lote.
        </p>
    </div>
);

export const CodigoAjusteBlock = ({ data, updateField }: { data: PrestacaoContas, updateField: Function }) => (
    <div className={CLS.SECTION}>
        <SectionHeader title="2. Cdigo do Ajuste" />
        <div className="max-w-md">
            <Field label="Cdigo do Ajuste (Fornecido pelo Tribunal)" value={data.codigo_ajuste} onChange={(v:any) => updateField('codigo_ajuste', v)} />
        </div>
        <p className="text-xs text-slate-500 mt-2">
            Este cdigo identifica unicamente o Convnio/Parceria no sistema Audesp.
        </p>
    </div>
);

export const RetificacaoBlock = ({ data, updateField }: { data: PrestacaoContas, updateField: Function }) => (
    <div className={CLS.SECTION}>
        <SectionHeader title="3. Retificao" />
        <div className="bg-orange-50 p-4 rounded border border-orange-200 flex items-center gap-3">
            <input 
                type="checkbox" 
                className={CLS.CHECKBOX} 
                checked={data.retificacao} 
                onChange={e => updateField('retificacao', e.target.checked)} 
            />
            <div>
                <label className="font-bold text-slate-800 text-sm">Este envio  uma RETIFICAO?</label>
                <p className="text-xs text-slate-500">Marque apenas se voc j enviou uma prestao para este ms e deseja corrigir dados.</p>
            </div>
        </div>
    </div>
);
